import { Editor, useEditorState } from '@tiptap/react';
import { memo } from 'react';
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Highlighter,
  Heading1,
  Heading2,
  Heading3,
  Quote,
  List,
  ListOrdered,
  CheckSquare,
  Table,
  Image,
  Minus,
  Undo,
  Redo,
  Copy,
  Info,
  AlertTriangle,
  AlertCircle,
  CheckCircle,
  FileText,
  Code2,
  Rows,
  Columns,
  Plus,
  Trash2,
  ToggleLeft,
  Palette,
  X,
  MoreHorizontal,
  Type,
  ListIcon,
  PlusCircle,
  IndentIncrease,
  IndentDecrease,
} from 'lucide-react';
import { useCallback, useState, useMemo } from 'react';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
  DropdownMenuSub,
  DropdownMenuSubTrigger,
  DropdownMenuSubContent,
} from '@/components/ui/dropdown-menu';
import { Button } from '@/components/ui/button';
import { Tooltip, TooltipContent, TooltipTrigger } from '@/components/ui/tooltip';

/*
 * DESIGN: Dark Mode Craftsman
 * Mobile-responsive toolbar with collapsible groups
 * Touch-friendly buttons with proper sizing
 */

interface EditorToolbarProps {
  editor: Editor;
  onCopyMarkdown?: () => void;
  onOpenLinkPopover?: () => void;
  onOpenFindReplace?: () => void;
  className?: string;
  disabledFeatures?: {
    tables?: boolean;
    images?: boolean;
    codeBlocks?: boolean;
    taskLists?: boolean;
    callouts?: boolean;
    datePills?: boolean;
    wikiLinks?: boolean;
    collapsibleHeadings?: boolean;
    slashCommands?: boolean;
    markdownPaste?: boolean;
    dragAndDrop?: boolean;
  };
}

interface ToolbarButtonProps {
  onClick: () => void;
  isActive?: boolean;
  disabled?: boolean;
  children: React.ReactNode;
  tooltip?: string;
}

const ToolbarButton = ({ onClick, isActive, disabled, children, tooltip }: ToolbarButtonProps) => {
  const button = (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`
        flex items-center justify-center w-9 h-9 sm:w-8 sm:h-8 rounded-md
        transition-all duration-100 ease-out touch-manipulation
        ${isActive 
          ? 'bg-primary text-primary-foreground' 
          : 'bg-transparent text-foreground hover:bg-secondary active:bg-secondary/80'
        }
        ${disabled ? 'opacity-50 cursor-not-allowed' : ''}
      `}
    >
      {children}
    </button>
  );

  if (tooltip) {
    return (
      <Tooltip>
        <TooltipTrigger asChild>{button}</TooltipTrigger>
        <TooltipContent side="bottom" className="text-xs hidden sm:block">
          {tooltip}
        </TooltipContent>
      </Tooltip>
    );
  }

  return button;
};

const Divider = () => (
  <div className="w-px h-6 bg-border mx-0.5 hidden sm:block" />
);

export const EditorToolbar = memo(function EditorToolbar({ editor, onCopyMarkdown, onOpenLinkPopover, className = '' }: EditorToolbarProps) {
  const [imageUrl, setImageUrl] = useState('');
  const [showImageInput, setShowImageInput] = useState(false);

  // Performance: Use useEditorState to only re-render when relevant toolbar states change
  // instead of re-rendering on every transaction
  const editorState = useEditorState({
    editor,
    selector: ({ editor: e }: { editor: Editor }) => ({
      canUndo: e.can().undo(),
      canRedo: e.can().redo(),
      isBold: e.isActive('bold'),
      isItalic: e.isActive('italic'),
      isUnderline: e.isActive('underline'),
      isStrike: e.isActive('strike'),
      isCode: e.isActive('code'),
      isHighlight: e.isActive('highlight'),
      isH1: e.isActive('heading', { level: 1 }),
      isH2: e.isActive('heading', { level: 2 }),
      isH3: e.isActive('heading', { level: 3 }),
      isBlockquote: e.isActive('blockquote'),
      isBulletList: e.isActive('bulletList'),
      isOrderedList: e.isActive('orderedList'),
      isTaskList: e.isActive('taskList'),
      isCodeBlock: e.isActive('codeBlock'),
      isLink: e.isActive('link'),
    }),
  });

  const addImage = useCallback(() => {
    if (imageUrl) {
      editor.chain().focus().setImage({ src: imageUrl }).run();
      setImageUrl('');
      setShowImageInput(false);
    }
  }, [editor, imageUrl]);

  const addTable = useCallback(() => {
    editor.chain().focus().insertTable({ rows: 3, cols: 3, withHeaderRow: true }).run();
  }, [editor]);

  const addCallout = useCallback((type: 'info' | 'warning' | 'error' | 'success' | 'note') => {
    editor.chain().focus().insertCallout({ type }).run();
  }, [editor]);

  return (
    <div className={`flex items-center gap-0.5 sm:gap-1 px-2 sm:px-3 py-1.5 sm:py-2 border-b border-border bg-card/50 overflow-x-auto scrollbar-hide ${className}`}>
      {/* Undo/Redo - Always visible */}
      <ToolbarButton
        onClick={() => editor.chain().focus().undo().run()}
        disabled={!editorState?.canUndo}
        tooltip="Undo (Ctrl+Z)"
      >
        <Undo size={18} className="sm:w-4 sm:h-4" />
      </ToolbarButton>
      <ToolbarButton
        onClick={() => editor.chain().focus().redo().run()}
        disabled={!editorState?.canRedo}
        tooltip="Redo (Ctrl+Shift+Z)"
      >
        <Redo size={18} className="sm:w-4 sm:h-4" />
      </ToolbarButton>

      <Divider />

      {/* Text formatting - Visible on desktop, dropdown on mobile */}
      <div className="hidden sm:flex items-center gap-0.5">
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleBold().run()}
          isActive={editorState?.isBold}
          tooltip="Bold (Ctrl+B)"
        >
          <Bold size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleItalic().run()}
          isActive={editorState?.isItalic}
          tooltip="Italic (Ctrl+I)"
        >
          <Italic size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleUnderline().run()}
          isActive={editorState?.isUnderline}
          tooltip="Underline (Ctrl+U)"
        >
          <Underline size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleStrike().run()}
          isActive={editorState?.isStrike}
          tooltip="Strikethrough"
        >
          <Strikethrough size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleCode().run()}
          isActive={editorState?.isCode}
          tooltip="Inline Code (Ctrl+E)"
        >
          <Code size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleHighlight().run()}
          isActive={editorState?.isHighlight}
          tooltip="Highlight"
        >
          <Highlighter size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => onOpenLinkPopover?.()}
          isActive={editorState?.isLink}
          tooltip="Link (Ctrl+K)"
        >
          <Link size={16} />
        </ToolbarButton>
      </div>

      {/* Mobile: Text formatting dropdown */}
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" size="sm" className="h-9 w-9 p-0 sm:hidden">
            <Type size={18} />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-48">
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleBold().run()}>
            <Bold size={16} className="mr-2" /> Bold
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleItalic().run()}>
            <Italic size={16} className="mr-2" /> Italic
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleUnderline().run()}>
            <Underline size={16} className="mr-2" /> Underline
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleStrike().run()}>
            <Strikethrough size={16} className="mr-2" /> Strikethrough
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleCode().run()}>
            <Code size={16} className="mr-2" /> Inline Code
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleHighlight().run()}>
            <Highlighter size={16} className="mr-2" /> Highlight
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => onOpenLinkPopover?.()}>
            <Link size={16} className="mr-2" /> Link
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      <Divider />

      {/* Headings dropdown */}
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" size="sm" className="h-9 sm:h-8 px-1.5 sm:px-2 gap-1">
            <Heading1 size={18} className="sm:w-4 sm:h-4" />
            <span className="text-xs hidden sm:inline">Heading</span>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start">
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()}>
            <Heading1 size={16} className="mr-2" /> Heading 1
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()}>
            <Heading2 size={16} className="mr-2" /> Heading 2
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()}>
            <Heading3 size={16} className="mr-2" /> Heading 3
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <DropdownMenuItem onClick={() => editor.chain().focus().setParagraph().run()}>
            <FileText size={16} className="mr-2" /> Paragraph
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      <Divider />

      {/* Lists - Visible on desktop, dropdown on mobile */}
      <div className="hidden md:flex items-center gap-0.5">
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleBulletList().run()}
          isActive={editorState?.isBulletList}
          tooltip="Bullet List"
        >
          <List size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleOrderedList().run()}
          isActive={editorState?.isOrderedList}
          tooltip="Numbered List"
        >
          <ListOrdered size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleTaskList().run()}
          isActive={editorState?.isTaskList}
          tooltip="Task List"
        >
          <CheckSquare size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleBlockquote().run()}
          isActive={editorState?.isBlockquote}
          tooltip="Quote"
        >
          <Quote size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().toggleCodeBlock().run()}
          isActive={editorState?.isCodeBlock}
          tooltip="Code Block"
        >
          <Code2 size={16} />
        </ToolbarButton>
        <Divider />
        <ToolbarButton
          onClick={() => {
            // Check if in task list or regular list and indent accordingly
            if (editorState?.isTaskList) {
              editor.chain().focus().sinkListItem('taskItem').run();
            } else if (editorState?.isBulletList || editorState?.isOrderedList) {
              editor.chain().focus().sinkListItem('listItem').run();
            }
          }}
          disabled={!editorState?.isBulletList && !editorState?.isOrderedList && !editorState?.isTaskList}
          tooltip="Indent (Tab)"
        >
          <IndentIncrease size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => {
            // Check if in task list or regular list and outdent accordingly
            if (editorState?.isTaskList) {
              editor.chain().focus().liftListItem('taskItem').run();
            } else if (editorState?.isBulletList || editorState?.isOrderedList) {
              editor.chain().focus().liftListItem('listItem').run();
            }
          }}
          disabled={!editorState?.isBulletList && !editorState?.isOrderedList && !editorState?.isTaskList}
          tooltip="Outdent (Shift+Tab)"
        >
          <IndentDecrease size={16} />
        </ToolbarButton>
      </div>

      {/* Mobile: Lists dropdown */}
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" size="sm" className="h-9 w-9 p-0 md:hidden">
            <ListIcon size={18} />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-48">
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleBulletList().run()}>
            <List size={16} className="mr-2" /> Bullet List
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleOrderedList().run()}>
            <ListOrdered size={16} className="mr-2" /> Numbered List
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleTaskList().run()}>
            <CheckSquare size={16} className="mr-2" /> Task List
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleBlockquote().run()}>
            <Quote size={16} className="mr-2" /> Quote
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().toggleCodeBlock().run()}>
            <Code2 size={16} className="mr-2" /> Code Block
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <DropdownMenuItem 
            onClick={() => {
              if (editorState?.isTaskList) {
                editor.chain().focus().sinkListItem('taskItem').run();
              } else if (editorState?.isBulletList || editorState?.isOrderedList) {
                editor.chain().focus().sinkListItem('listItem').run();
              }
            }}
            disabled={!editorState?.isBulletList && !editorState?.isOrderedList && !editorState?.isTaskList}
          >
            <IndentIncrease size={16} className="mr-2" /> Indent
          </DropdownMenuItem>
          <DropdownMenuItem 
            onClick={() => {
              if (editorState?.isTaskList) {
                editor.chain().focus().liftListItem('taskItem').run();
              } else if (editorState?.isBulletList || editorState?.isOrderedList) {
                editor.chain().focus().liftListItem('listItem').run();
              }
            }}
            disabled={!editorState?.isBulletList && !editorState?.isOrderedList && !editorState?.isTaskList}
          >
            <IndentDecrease size={16} className="mr-2" /> Outdent
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>

      <Divider />

      {/* Insert elements - Inline on large screens, dropdown on smaller screens */}
      {/* Desktop: Show Table, Image, HR, and Callout buttons inline */}
      <div className="hidden lg:flex items-center gap-0.5">
        <ToolbarButton
          onClick={addTable}
          tooltip="Insert Table (3×3)"
        >
          <Table size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => setShowImageInput(true)}
          tooltip="Insert Image"
        >
          <Image size={16} />
        </ToolbarButton>
        <ToolbarButton
          onClick={() => editor.chain().focus().setHorizontalRule().run()}
          tooltip="Horizontal Rule"
        >
          <Minus size={16} />
        </ToolbarButton>
        {/* Callout dropdown for desktop inline */}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <button
              className="flex items-center justify-center w-8 h-8 rounded-md transition-all duration-100 ease-out touch-manipulation bg-transparent text-foreground hover:bg-secondary active:bg-secondary/80"
              title="Insert Callout"
            >
              <Info size={16} />
            </button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="start">
            <DropdownMenuItem onClick={() => addCallout('info')}>
              <Info size={16} className="mr-2 text-blue-400" /> Info
            </DropdownMenuItem>
            <DropdownMenuItem onClick={() => addCallout('warning')}>
              <AlertTriangle size={16} className="mr-2 text-yellow-400" /> Warning
            </DropdownMenuItem>
            <DropdownMenuItem onClick={() => addCallout('error')}>
              <AlertCircle size={16} className="mr-2 text-red-400" /> Error
            </DropdownMenuItem>
            <DropdownMenuItem onClick={() => addCallout('success')}>
              <CheckCircle size={16} className="mr-2 text-green-400" /> Success
            </DropdownMenuItem>
            <DropdownMenuItem onClick={() => addCallout('note')}>
              <FileText size={16} className="mr-2 text-purple-400" /> Note
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>

      {/* Mobile/Tablet: Insert elements dropdown */}
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="ghost" size="sm" className="h-9 sm:h-8 px-1.5 sm:px-2 gap-1 lg:hidden">
            <PlusCircle size={18} className="sm:w-4 sm:h-4" />
            <span className="text-xs hidden sm:inline">Insert</span>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-56">
          <DropdownMenuItem onClick={addTable}>
            <Table size={16} className="mr-2" /> Table (3×3)
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => setShowImageInput(true)}>
            <Image size={16} className="mr-2" /> Image
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => editor.chain().focus().setHorizontalRule().run()}>
            <Minus size={16} className="mr-2" /> Horizontal Rule
          </DropdownMenuItem>
          <DropdownMenuSeparator />
          <DropdownMenuSub>
            <DropdownMenuSubTrigger>
              <Info size={16} className="mr-2" /> Callout
            </DropdownMenuSubTrigger>
            <DropdownMenuSubContent>
              <DropdownMenuItem onClick={() => addCallout('info')}>
                <Info size={16} className="mr-2 text-blue-400" /> Info
              </DropdownMenuItem>
              <DropdownMenuItem onClick={() => addCallout('warning')}>
                <AlertTriangle size={16} className="mr-2 text-yellow-400" /> Warning
              </DropdownMenuItem>
              <DropdownMenuItem onClick={() => addCallout('error')}>
                <AlertCircle size={16} className="mr-2 text-red-400" /> Error
              </DropdownMenuItem>
              <DropdownMenuItem onClick={() => addCallout('success')}>
                <CheckCircle size={16} className="mr-2 text-green-400" /> Success
              </DropdownMenuItem>
              <DropdownMenuItem onClick={() => addCallout('note')}>
                <FileText size={16} className="mr-2 text-purple-400" /> Note
              </DropdownMenuItem>
            </DropdownMenuSubContent>
          </DropdownMenuSub>
        </DropdownMenuContent>
      </DropdownMenu>

      {/* Table controls dropdown - Only when in table */}
      {editor.isActive('table') && (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button 
              variant="ghost" 
              size="sm" 
              className="h-9 sm:h-8 px-1.5 sm:px-2 gap-1 bg-primary/10"
            >
              <Table size={18} className="sm:w-4 sm:h-4" />
              <span className="text-xs hidden sm:inline">Table</span>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="start" className="w-56">
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().addColumnBefore().run()}
              disabled={!editor.can().addColumnBefore()}
            >
              <Columns size={16} className="mr-2" /> Add Column Before
            </DropdownMenuItem>
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().addColumnAfter().run()}
              disabled={!editor.can().addColumnAfter()}
            >
              <Columns size={16} className="mr-2" /> Add Column After
            </DropdownMenuItem>
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().deleteColumn().run()}
              disabled={!editor.can().deleteColumn()}
            >
              <Trash2 size={16} className="mr-2 text-destructive" /> Delete Column
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().addRowBefore().run()}
              disabled={!editor.can().addRowBefore()}
            >
              <Rows size={16} className="mr-2" /> Add Row Before
            </DropdownMenuItem>
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().addRowAfter().run()}
              disabled={!editor.can().addRowAfter()}
            >
              <Rows size={16} className="mr-2" /> Add Row After
            </DropdownMenuItem>
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().deleteRow().run()}
              disabled={!editor.can().deleteRow()}
            >
              <Trash2 size={16} className="mr-2 text-destructive" /> Delete Row
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().toggleHeaderRow().run()}
              disabled={!editor.can().toggleHeaderRow()}
            >
              <ToggleLeft size={16} className="mr-2" /> Toggle Header Row
            </DropdownMenuItem>
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().toggleHeaderColumn().run()}
              disabled={!editor.can().toggleHeaderColumn()}
            >
              <ToggleLeft size={16} className="mr-2" /> Toggle Header Column
            </DropdownMenuItem>
            <DropdownMenuSeparator />
            <DropdownMenuSub>
              <DropdownMenuSubTrigger>
                <Palette size={16} className="mr-2" /> Cell Background
              </DropdownMenuSubTrigger>
              <DropdownMenuSubContent className="w-48">
                <div className="p-2">
                  <div className="text-xs text-muted-foreground mb-2">Quick Colors</div>
                  <div className="grid grid-cols-6 gap-1 mb-2">
                    {[
                      { color: '#ef4444', name: 'Red' },
                      { color: '#f97316', name: 'Orange' },
                      { color: '#eab308', name: 'Yellow' },
                      { color: '#22c55e', name: 'Green' },
                      { color: '#3b82f6', name: 'Blue' },
                      { color: '#8b5cf6', name: 'Purple' },
                      { color: '#ec4899', name: 'Pink' },
                      { color: '#14b8a6', name: 'Teal' },
                      { color: '#6366f1', name: 'Indigo' },
                      { color: '#64748b', name: 'Slate' },
                      { color: '#1e293b', name: 'Dark' },
                      { color: '#f1f5f9', name: 'Light' },
                    ].map(({ color, name }) => (
                      <button
                        key={color}
                        onClick={() => editor.chain().focus().setCellBackground(color).run()}
                        className="w-6 h-6 rounded border border-border hover:scale-110 transition-transform touch-manipulation"
                        style={{ backgroundColor: color }}
                        title={name}
                      />
                    ))}
                  </div>
                  <div className="text-xs text-muted-foreground mb-1">Custom Color</div>
                  <input
                    type="color"
                    defaultValue="#3b82f6"
                    onChange={(e) => editor.chain().focus().setCellBackground(e.target.value).run()}
                    className="w-full h-8 rounded cursor-pointer border border-border"
                    style={{ backgroundColor: 'transparent' }}
                  />
                </div>
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={() => editor.chain().focus().unsetCellBackground().run()}>
                  <X size={16} className="mr-2" /> Remove Background
                </DropdownMenuItem>
              </DropdownMenuSubContent>
            </DropdownMenuSub>
            <DropdownMenuSeparator />
            <DropdownMenuItem 
              onClick={() => editor.chain().focus().deleteTable().run()}
              disabled={!editor.can().deleteTable()}
              className="text-destructive"
            >
              <Trash2 size={16} className="mr-2" /> Delete Table
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      )}

      {/* Image URL input modal */}
      {showImageInput && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 sm:relative sm:inset-auto sm:bg-transparent sm:p-0">
          <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2 p-4 sm:p-0 bg-card sm:bg-secondary rounded-lg sm:rounded-md w-full max-w-sm sm:max-w-none sm:w-auto">
            <input
              type="url"
              placeholder="Image URL..."
              value={imageUrl}
              onChange={(e) => setImageUrl(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  addImage();
                }
                if (e.key === 'Escape') {
                  setShowImageInput(false);
                  setImageUrl('');
                }
              }}
              className="bg-secondary sm:bg-transparent border sm:border-none rounded px-3 py-2 sm:py-0 outline-none text-sm w-full sm:w-40"
              autoFocus
            />
            <div className="flex gap-2 sm:gap-1">
              <button
                onClick={addImage}
                className="flex-1 sm:flex-none px-4 sm:px-2 py-2 sm:py-0.5 text-sm sm:text-xs bg-primary text-primary-foreground rounded"
              >
                Add
              </button>
              <button
                onClick={() => {
                  setShowImageInput(false);
                  setImageUrl('');
                }}
                className="flex-1 sm:flex-none px-4 sm:px-2 py-2 sm:py-0.5 text-sm sm:text-xs text-muted-foreground bg-secondary sm:bg-transparent rounded"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Spacer */}
      <div className="flex-1 min-w-2" />

      {/* Copy as Markdown */}
      {onCopyMarkdown && (
        <Tooltip>
          <TooltipTrigger asChild>
            <Button 
              variant="ghost" 
              size="sm" 
              className="h-9 sm:h-8 px-2 gap-1 shrink-0" 
              onClick={onCopyMarkdown}
            >
              <Copy size={18} className="sm:w-4 sm:h-4" />
              <span className="text-xs hidden md:inline">Copy MD</span>
            </Button>
          </TooltipTrigger>
          <TooltipContent side="bottom" className="text-xs">
            Copy content as Markdown
          </TooltipContent>
        </Tooltip>
      )}
    </div>
  );
});

export default EditorToolbar;
